/* ==========================================================================
   Widgets styles
   ========================================================================== */
/* ==========================================================================
   Instagram widget style - begin
   ========================================================================== */
aside.mkdf-sidebar .widget.widget_mkdf_instagram_widget .mkdf-widget-title,
.wpb_widgetised_column .widget.widget_mkdf_instagram_widget .mkdf-widget-title {
  margin: 0 0 25px;
}

@media only screen and (max-width: 680px) {
  .mkdf-footer-middle-holder .widget.widget_mkdf_instagram_widget {
    margin-top: 50px !important;
  }
}

.mkdf-instagram-feed {
  list-style: none;
  padding: 0;
  margin: 0;
}

.mkdf-instagram-feed li {
  float: left;
  box-sizing: border-box;
  border: none !important;
}

.mkdf-instagram-feed li a {
  position: relative;
  display: block;
  overflow: hidden;
}

.mkdf-instagram-feed li a:hover:after {
  opacity: 1;
}

.mkdf-instagram-feed li a:after {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  content: '';
  background-color: rgba(30, 30, 30, 0.5);
  opacity: 0;
  transition: opacity .2s ease-in-out;
}

.mkdf-instagram-feed li a:after {
  background-color: rgba(0, 0, 0, 0.5);
}

.mkdf-instagram-feed li a .mkdf-instagram-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  color: #fff;
  opacity: 0;
  z-index: 1;
  transition: opacity .2s ease-in-out;
  transform: translate(-50%, -50%);
}

.mkdf-instagram-feed li a:hover .mkdf-instagram-icon {
  opacity: 1;
}

.mkdf-instagram-feed li img {
  width: 100%;
  display: block;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-no-space {
  margin: 0;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-no-space li {
  padding: 0 0px;
  margin: 0 0 0px !important;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-tiny-space {
  margin: 0 -5px -10px;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-tiny-space li {
  padding: 0 5px;
  margin: 0 0 10px !important;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-small-space {
  margin: 0 -10px -20px;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-small-space li {
  padding: 0 10px;
  margin: 0 0 20px !important;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-normal-space {
  margin: 0 -15px -30px;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-normal-space li {
  padding: 0 15px;
  margin: 0 0 30px !important;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-col-2 li {
  width: 50%;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-col-3 li {
  width: 33.33333%;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-col-4 li {
  width: 25%;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-col-6 li {
  width: 16.66667%;
}

.mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-col-9 li {
  width: 11.11111%;
}

.mkdf-instagram-feed.mkdf-instagram-carousel li {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 0;
}

.mkdf-instagram-feed.mkdf-instagram-carousel li a {
  position: relative;
  display: block;
  height: 100%;
}

/* ==========================================================================
   Instagram widget style - end
   ========================================================================== */
